'use client';
import { useState } from 'react';
import { HeaderMegaMenu } from './Header/HeaderMegaMenu';
import { Main } from './Main/Main';

export default function App() {
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  // Function to update the selected image
  const handleImageSelection = (image: string) => {
    if (!image) {
        setSelectedImage('../pics/EmperorPenguinBabyImage.jpeg')
    }
    else{
        setSelectedImage(image);
    }

    console.log(selectedImage);
  };

  return (
    <>
      {/* Pass down the handler to update the selected image */}
      <HeaderMegaMenu onImageClick={handleImageSelection} />
      <Main selectedImage={selectedImage || '../pics/EmperorPenguinBabyImage.jpeg'} />
    </>
  );
}
